import React from 'react';
import { CalendarProps } from 'shared/types/calendar';

import { ControlledInputTimeProps } from '../InputTime/ControlledInputTime';
import { ControlledInputDateTime, ControlledInputDateTimeProps } from './ControlledInputDateTime';
import { useInputDateTime } from './useInputDateTime';

export type InputDateTimeProps = Omit<
ControlledInputDateTimeProps,
'value' | 'onChange' | 'inputCalendar' | 'inputTime'> & {

  /** Свойства для компонента InputTime */
  inputCalendar?: CalendarProps;

  /** Свойства для компонента InputTime */
  inputTime?: ControlledInputTimeProps;

  /** Начальное значение */
  value?: Date;

  /** Коллбэк, возвращающий выбранную дату и время */
  onChange?: (value?: Date) => void;
};

export function InputDateTime({
  inputCalendar,
  inputTime,
  value,
  onChange,
  ...rest
}: InputDateTimeProps): JSX.Element {
  const [internalValue, setInternalValue] = useInputDateTime({
    value,
    onChange,
    minDate: inputCalendar?.minDate,
    maxDate: inputCalendar?.maxDate
  });

  return (
    <ControlledInputDateTime
      { ...rest }
      value={ internalValue }
      onChange={ setInternalValue }
      inputCalendar={ inputCalendar }
      inputTime={ inputTime }
    />
  );
}
